.App {
  text-align: center;
}

html, body {
  height: 100%;
}

button:hover{
  cursor: pointer;
}

body{
  height: 100%;
  padding: 1.2vw;
  
  background-color: rgb(119, 91, 91);
}

.popUpBg{
  height: 100%;
  width:100%;
  position:absolute;
  display: flex;
  justify-content: center;

}

.popUp{
  height: auto;
  width: 50%;
  background-color: white;
  margin-top: 15%;
  position:absolute;
  border-radius: 1vw;
}











.mainDiv{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  background-color: rgb(46, 31, 31);
}






