

.dishDiv{
    width: 78%;
    
    padding-left:1%;
    padding-right:1%;
    margin-top: 8%;
    
    font-size: 2.3vh;
    
    animation: 1s ease-out 0s 1 slider;
    
}

.dishHelper{
    margin-bottom: 1.5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(70, 138, 126);
    border-radius: 15px;
    border-style: solid;
    width:100%;
    height: 100%;
}

@keyframes slider {
 
  0% {
      transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }

}